
import React, { useState } from 'react'

export default function Input({onadd}) {

    const [data, setdata] = useState([{
        item:'',
        units:'',
        unitPrice:'',
    }])

    function handle(e){

        setdata({...data,[e.target.name]:e.target.value})

    }

    return (
        <div>
            <input type="text"  name="item" onChange={(e)=>handle(e)}/>
            <input type="text"   name="units" onChange={(e)=>handle(e)}/>
            <input type="text"   name="unitPrice" onChange={(e)=>handle(e)}/>
            <button type="submit" onClick={()=>{
                onadd(data)
                setdata({
                    item:'',
                    units:'',
                    unitPrice:''
                })}}> 
                Add Item </button>
            
            
        </div>
    )
}
