import Input from './components/input'
import './App.css';
import Table from './components/table'
import { useState } from 'react';
import Total from './components/total';



function App() {

  const onAdd = (data)=>{

      const id = Math.floor(Math.random() * 1000000); //on less value check for data
      const list = {
        id,
        item:data.item,
        units:data.units,
        unitPrice : data.unitPrice,
      }
      setgroceries([...groceries,list])

  }

  const deleteItem = (id)=>{
    
      const data  = groceries.filter(x=>x.id!==id)
      setgroceries(data)
     
  }

  const editItem = (id,value)=>{
    const key = Object.keys(value)[0]
    groceries[groceries.findIndex(g=>g.id===id)][key] = value[key]
    
    setgroceries([...groceries])

  }
  
  const [groceries, setgroceries] = useState([
    {
      id:1,
      item : "Bread",
      units: 12,
      unitPrice : 10
    },
    {
      id:2,
      item : "Milk",
      units: 5,
      unitPrice : 30
    }
  ])

  return (
    <div className="App">
      <h2> Grocery List</h2>
      <Input onadd={(data)=>onAdd(data)}/>
      <Table groceries={groceries} onDelete={(id)=>deleteItem(id)} onEdit={(id,value)=>editItem(id,value)}/>
      <Total groceries = {groceries}/>
    </div>
  );
}

export default App;
